<template><section>
<h2>微信JSSDK封装</h2>
<p>必须首先引入jssdk 不然会报错 当前没有集成 需要自行引入</p>
<pre v-pre=""><code class="hljs language-javascript">  index.html
  &lt;script src=<span class="hljs-string">"../static/jssdk/jweixin-1.4.0.js"</span>&gt;<span class="xml"><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></span>
  main.js
  <span class="hljs-keyword">import</span> { WxApi } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  Vue.use(WxApi)
  <span class="hljs-comment">// 访问 this.$wxApi</span>
</code></pre>
<h4>wxConfig 微信初始化</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWxConfig\&quot;&gt;点击微信初始化&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * 初始化微信配置\n     * @param {来源，JS接口列表（默认全部），开启调试模式（默认关闭）} {source, jsApiList, debug, succ, fail}\n     * 会直接发起微信授权的请求 等后台返回签名 然后进行微信授权 只能在微信环境下才能生效！\n     */\n    onClickWxConfig () {\n      this.$wxApi.wxConfig(\n        { source: '我爱多保鱼', debug: true, succ(res) { //source目前有我爱多保鱼和 多保鱼服务号\n\n        }, fail(res) {\n\n        }}\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWxConfig">点击微信初始化</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWxConfig"</span>&gt;</span>点击微信初始化<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * 初始化微信配置
     * @param {来源，JS接口列表（默认全部），开启调试模式（默认关闭）} {source, jsApiList, debug, succ, fail}
     * 会直接发起微信授权的请求 等后台返回签名 然后进行微信授权 只能在微信环境下才能生效！
     */</span>
    onClickWxConfig () {
      <span class="hljs-keyword">this</span>.$wxApi.wxConfig(
        { <span class="hljs-attr">source</span>: <span class="hljs-string">'我爱多保鱼'</span>, <span class="hljs-attr">debug</span>: <span class="hljs-literal">true</span>, succ(res) { <span class="hljs-comment">//source目前有我爱多保鱼和 多保鱼服务号</span>

        }, fail(res) {

        }}
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>wxShare 微信初始化分享</h4>
<p>该分享必须要在微信初始化调用  建议每个页面都进行初始化</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWxShare\&quot;&gt;点击微信初始化分享&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * 微信分享\n     * @param {分享标题} title\n     * @param {分享链接} link\n     * @param {分享图标} imgUrl\n     * @param {分享描述} desc\n     */\n    onClickWxShare() {\n      this.$wxApi.wxConfig(\n        { source: '我爱多保鱼', debug: true, succ:(res) =&gt; {\n          this.$wxApi.wxShare({ title: '你好', link: 'a', imgUrl: 'a', desc: 'a', succ(){}, fail(){} })\n        }, fail(res) {\n\n        }}\n      )\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWxShare">点击微信初始化分享</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWxShare"</span>&gt;</span>点击微信初始化分享<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * 微信分享
     * @param {分享标题} title
     * @param {分享链接} link
     * @param {分享图标} imgUrl
     * @param {分享描述} desc
     */</span>
    onClickWxShare() {
      <span class="hljs-keyword">this</span>.$wxApi.wxConfig(
        { <span class="hljs-attr">source</span>: <span class="hljs-string">'我爱多保鱼'</span>, <span class="hljs-attr">debug</span>: <span class="hljs-literal">true</span>, <span class="hljs-attr">succ</span>:<span class="hljs-function">(<span class="hljs-params">res</span>) =&gt;</span> {
          <span class="hljs-keyword">this</span>.$wxApi.wxShare({ <span class="hljs-attr">title</span>: <span class="hljs-string">'你好'</span>, <span class="hljs-attr">link</span>: <span class="hljs-string">'a'</span>, <span class="hljs-attr">imgUrl</span>: <span class="hljs-string">'a'</span>, <span class="hljs-attr">desc</span>: <span class="hljs-string">'a'</span>, succ(){}, fail(){} })
        }, fail(res) {

        }}
      )
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>wxPageInitShare 微信初始化分享配置</h4>
<p>该接口是对当前的微信分享进行进一步封装 达到更具当前路由可以从后台的hash表上查到当前对应的页面配置
这个建议在路由守卫或者是路由监听中调取 每次页面跳转时候调用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWxPageInitShare\&quot;&gt;页面初始化分享配置&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * 页面初始化分享配置\n     * @param {route地址} { path, succ, fail }\n     */\n    onClickWxPageInitShare() {\n      this.$wxApi.wxPageInitShare ({ path: this.$route.path, succ(){}, fail(){} }) \n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <base-button color="danger" @click="onClickWxPageInitShare">页面初始化分享配置</base-button>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWxPageInitShare"</span>&gt;</span>页面初始化分享配置<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * 页面初始化分享配置
     * @param {route地址} { path, succ, fail }
     */</span>
    onClickWxPageInitShare() {
      <span class="hljs-keyword">this</span>.$wxApi.wxPageInitShare ({ <span class="hljs-attr">path</span>: <span class="hljs-keyword">this</span>.$route.path, succ(){}, fail(){} }) 
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>wxChooseImage 拍照或从手机相册中选图接口</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;base-button color=\&quot;danger\&quot; @click=\&quot;onClickWxChooseImage\&quot;&gt;点击调用拍照&lt;/base-button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    /**\n     * 拍照或从手机相册中选图接口\n     * @param {张数,原图还是压缩图,相册还是相机} { count, sizeType, sourceType, succ, fail }\n     */\n    onClickWxChooseImage() {\n      this.$wxApi.wxChooseImage ({ succ() {}, fail() {} })\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
