  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWxChooseImage"</span>&gt;</span>点击调用拍照<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
