  <span class="hljs-tag">&lt;<span class="hljs-name">base-button</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"onClickWxPlayVoice"</span>&gt;</span>播放语音<span class="hljs-tag">&lt;/<span class="hljs-name">base-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    <span class="hljs-comment">/**
     * 点击播放语音 serverId,需要下载的音频的服务器端ID，由uploadVoice接口获得
     */</span>
    onClickWxPlayVoice() {
      <span class="hljs-keyword">this</span>.$wxApi.wxPlayVoice ({ <span class="hljs-attr">serverId</span>: <span class="hljs-string">'x'</span>, succ(){}, fail(){} })
    }
